'use strict';
import ProductActions from '../actions/ProductActions';

const products = [{
  id: 'Business Cards',
  settings: {
    options: [{
      label: 'White 14pt. Uncoated Flat Ink',
      value: 'White 14pt. Uncoated Flat Ink'
    }],
    config: [{
      header:'Card Size',
      description: '3.5 x 2in'
    }, {
      header:'Front side',
      description: 'Uploaded'
    }, {
      header:'Back side',
      description: 'blank'
    }],
    details: 'Business cards typically include a persons name, e-mail address, phone number, website, and company name.'
  },
  preview:{
    front: {
      thumb_url:'/images/business_card.png',
      url: '/images/business_card.png'
    },
    back: {
      thumb_url:'/images/business_card.png',
      url: '/images/business_card.png'
    }
  }
}];

module.exports = {
  getAllProducts() {
    // simulate retrieving data from a database
    setTimeout(() => {
      ProductActions.receiveAll(products);
    }, 4000);
    // simulate success callback
  }
};


